import { debug, info, warning, error } from "@actions/core";
export class GithubActionLog {
    debug(...parts) {
        debug(parts.join(" "));
    }
    info(...parts) {
        info(parts.join(" "));
    }
    warn(...parts) {
        warning(parts.join(" "));
    }
    error(...parts) {
        error(parts.join(" "));
    }
}
